html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@font-face {
  font-family: "Ubuntu";
  src: url("../fonts/ubuntu-r.woff2") format("woff2"), url("../fonts/ubuntu-r.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Centar Sans";
  src: url("../fonts/centar-sans.woff2") format("woff2"), url("../fonts/centar-sans.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Lato";
  src: url("../fonts/lato-regular.woff2") format("woff2"), url("../fonts/lato-regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@keyframes scroll {
  50% {
    transform: translateY(50%);
  }
}

@keyframes ripple {
  from {
    transform: scale(0);
  }
  to {
    transform: scale(2.5);
    opacity: 0;
  }
}

.ripple {
  border-radius: 50%;
  position: absolute;
  animation: ripple 0.7s linear;
}

.icon-arrow {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-burger {
  width: 1em;
  height: 1em;
  fill: none;
}

body {
  background: #FFF;
  font-family: 'Ubuntu', sans-serif;
  line-height: 1.4;
}

.btn {
  border: 0;
  outline: 0;
  display: inline-block;
  position: relative;
  overflow: hidden;
}

.btn_ts {
  background-color: transparent;
}

.full-l {
  width: 100%;
  height: 100vh;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: cover;
}

.block {
  display: flex;
}

.start {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  position: relative;
}

.start__in {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  min-width: 80%;
  text-align: center;
}

.start__title {
  font-size: 48px;
  color: #150d3f;
  padding: 10vh 0;
}

.start__title_white {
  color: #f2f2f2;
}

.start__btn {
  text-align: center;
}

.start__btn .btn {
  animation: scroll infinite 2s ease-out;
}

.start__btn .btn:hover {
  animation-play-state: paused;
}

.start__btn .icon-arrow {
  font-size: 72px;
  fill: #f2f2f2;
}

.header {
  background-color: #150d3f;
  padding: 0 20px;
  margin-bottom: 50px;
  height: 70px;
  display: flex;
}

.header__logo {
  width: 25%;
  margin: auto 0;
}

.header__logo img {
  height: 42px;
  display: block;
}

.header__nav {
  width: 75%;
  position: relative;
}

.header__icon {
  display: none;
  height: 100%;
  position: relative;
}

@media only screen and (max-width: 1023px) {
  .header__icon {
    display: block;
  }
}

.nav {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: flex-end;
}

@media only screen and (max-width: 1023px) {
  .nav {
    display: none;
  }
  .nav.is-active {
    display: block;
    height: auto;
    width: 100vw;
    right: -20px;
    position: absolute;
    top: 70px;
    background-color: rgba(21, 13, 63, 0.9);
    text-align: center;
  }
}

.nav__item {
  position: relative;
  overflow: hidden;
}

.nav__item a {
  text-decoration: none;
  font-weight: 500;
  letter-spacing: 0.4px;
  font-size: 18px;
  color: #fff142;
  display: block;
  height: 100%;
  padding: 0 20px;
  line-height: 70px;
  transition: all 0.2s ease;
}

.nav__item a:hover {
  opacity: .7;
}

.nav-toggle {
  position: absolute;
  top: 50%;
  right: 0;
  transform: translate(0, -50%);
  width: 40px;
  height: 28px;
}

.nav-toggle span {
  display: block;
  width: 100%;
  height: 4px;
  border-radius: 6px;
  transition: all 0.2s ease;
  position: absolute;
  background-color: rgba(242, 242, 242, 0.8);
}

.nav-toggle span:nth-child(1) {
  top: 0;
  transform-origin: left center;
}

.nav-toggle span:nth-child(2) {
  top: 12px;
  transform-origin: left center;
}

.nav-toggle span:nth-child(3) {
  top: 24px;
  transform-origin: left center;
}

.nav-toggle.is-open span:nth-child(1) {
  transform: rotate(45deg);
  top: -2px;
}

.nav-toggle.is-open span:nth-child(2) {
  width: 0;
  opacity: 0;
}

.nav-toggle.is-open span:nth-child(3) {
  transform: rotate(-45deg);
  top: 26px;
}
