.start {
	width: 100%;
	height: 100%;
	background-color: rgba(0,0,0,.3);
	position: relative;
	&__in {
		@include vcenter;
		min-width: 80%;
		text-align: center; }
	&__title {
		font-size: 48px;
		color: $violet;
		padding: 10vh 0;
		&_white {
			color: $white; } }
	&__btn {
		text-align: center;
		.btn {
			animation: scroll infinite 2s ease-out;
			&:hover {
				animation-play-state: paused; } }
		.icon-arrow {
			font-size: 72px;
			fill: $white; } } }


