$height: 70px;

.header {
	background-color: $violet;
	padding: 0 20px;
	margin-bottom: 50px;
	height: $height;
	display: flex;
	&__logo {
		width: 25%;
		margin: auto 0;
		img {
			height: $height * 0.6;
			display: block; } }
	&__nav {
		width: 75%;
		position: relative; }
	&__icon {
		display: none;
		height: 100%;
		position: relative;
		@include md {
			display: block; } } }
.nav {
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: flex-end;
	@include md {
		display: none;
		&.is-active {
			display: block;
			height: auto;
			width: 100vw;
			right: -20px;
			position: absolute;
			top: $height;
			background-color: transparentize($violet, .1);
			text-align: center; } }
	&__item {
		position: relative;
		overflow: hidden;
		a {
			text-decoration: none;
			font-weight: 500;
			letter-spacing: 0.4px;
			font-size: 18px;
			color: $yellow;
			display: block;
			height: 100%;
			padding: 0 20px;
			line-height: $height;
			@include trans;
			&:hover {
				opacity: .7; } } } }
.nav-toggle {
	position: absolute;
	top: 50%;
	right: 0;
	transform: translate(0, -50%);
	width: 40px;
	height: 28px;
	span {
		display: block;
		width: 100%;
		height: 4px;
		border-radius: 6px;
		@include trans;
		position: absolute;
		background-color: transparentize($white, .2); }
	span:nth-child(1) {
		top: 0;
		transform-origin: left center; }
	span:nth-child(2) {
		top: 12px;
		transform-origin: left center; }
	span:nth-child(3) {
		top: 24px;
		transform-origin: left center; }
	&.is-open {
		span:nth-child(1) {
			transform: rotate(45deg);
			top: -2px; }
		span:nth-child(2) {
			width: 0;
			opacity: 0; }
		span:nth-child(3) {
			transform: rotate(-45deg);
			top: 26px; } } }




